    position: relative;

    .help-block {
        position: absolute;
        top: 10px;

        span.help-text {
            display: inline-block;
            margin-top: 4px;
            margin-right: 6px;
            a {
                text-decoration: none;
            }
        }

        span.dashicons {
            font-size: 25px;
        }
    }

    th.title {
        width: 35%;
    }

    td.status {
        span {
            line-height: 2.5em;
            padding: 5px 8px;
            border-radius: 4px;
        }

        .publish {
            background: #c6e1c6;
            color: #5b841b;
        }

        .pending {
            background: #f8dda7;
            color: #94660c
        }

        .trash {
            background: #eba3a3;
            color: #761919
        }

        .draft {
            background: #e5e5e5;
            color: #761919
        }

        .future {
            background: #8080805c;
            color: #000000e0;
        }
    }
    .dokan-modal {
        header.modal-header  {
            h1 {
                width: 90%;
            }
        }
    }
    .announcement-modal-content {
        height: 200px;
    }
}

@media only screen and (max-width: 600px) {
    .dokan-announcement-wrapper {
        .help-block {
            top: 45px !important;
            left: 0 !important;
        }

        .subsubsub {
            margin-top: 20px;
        }

        table {
            td.title, td.content {
                display: table-cell !important;
            }

            th:not(.check-column):not(.title):not(.content):not(.send_to) {
                display: none;
            }

            td:not(.check-column):not(.title):not(.content):not(.send_to) {
                display: none;
            }

            th.column, td.column {
                width: auto;
            }

            td.manage-column.column-cb.check-column {
                padding-right: 15px;
            }
            td.selected_user_role{
                word-wrap: break-word;
            }
        }
    }
}
.dokan-settings-wrap .dashicons.dashicons-money-alt {
    color: #ff0099;
}


.help-block-rtl{
    left: 10px;
}
.help-block-ltr{
    right: 10px;
}
</style>
