<template>
    <div class="dokan-announcement-form-wrapper">
        <tabs/>
        <h1 class="wp-heading-inline">{{ pageTitle }}</h1>
        <a @click="$router.go(-1)" class="page-title-action">{{ __( 'Go Back', 'dokan' ) }}</a>

        <hr class="wp-header-end">

        <div id='dokan-quote-notice'>
            <div v-if="quoteNoticeClass" :class="'quote-notice ' + quoteNoticeClass">
                <span v-html="quoteNoticeText"></span>
