        </div>

        <form action="" method="post" id="post">
            <div id="poststuff">
                <div id="post-body" class="metabox-holder flex sm:flex-col md:flex-row gap-5">
                    <div class='md:w-[80%]'>
                        <div class="!mb-0 post-body-content" :class="checkRtl">
                            <div id="titlediv">
                                <div id="titlewrap">
                                    <input type="text" v-model="quote.quote_title" name="post_title" size="30" id="title" autocomplete="off" :placeholder="__('Enter quote title', 'dokan')" required="required">
                                </div>
                                <div class="inside"></div>
