                                    <ul class="error-message" v-if="errorMessage.length > 0" v-for="error in errorMessage">
                                        <li>{{ error }}</li>
                                    </ul>
                                    <table class="form-table announcement-meta-options">
                                        <tbody>
                                        <tr>
                                            <th>{{ __('Quote user', 'dokan') }}</th>
                                            <td>
                                                <multiselect  @select="onSelectCustomer" autocomplete="off" :showNoOptions="false" v-model="quote.user_id" id="ajax" label="name" track-by="id" :placeholder="__('Type to search', 'dokan')" open-direction="bottom" :options="customers" @search-change="asyncCustomerFind" :clear-on-select="true" :close-on-select="true" :searchable="true" :hide-selected="true"></multiselect>
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>
